import React, { Component } from 'react'
import _ from 'lodash'
import {Container,Divider,Dropdown,Grid,Header,Icon,Image,List,Menu,Segment,Visibility,Button,Message} from 'semantic-ui-react'
import MediaQuery from 'react-responsive'
import {Profile} from './Profile.js'
import {Resume} from './Resume.js'
import {Works} from './Works.js'
import './Body.css'
import {CSSTransitionGroup} from 'react-transition-group'
import Media from 'react-media'

const greetingWrapperUp = {marginTop:"-5%"}//used for shifting the greeting upwards
const greetingWrapperDefault = {marginTop:"2.5%"}//used for shifting the greeting to default position
const greetingExpanded = {marginTop:"5.2%",marginLeft:"39%",position:"fixed", zIndex:100}
const buttonResumeExpanded = {marginTop:"17%", marginLeft:"0%"}
const buttonGithubExpanded = {marginTop:"-0.38%", marginLeft:"2%"}
const aboutMeExpanded = {marginTop:"2.5%", fontSize:"120%"}
const menuButton1Default = {marginLeft: "60%", fontFamily: "Montserrat, sans-serif", fontSize: "250%"}
const menuButton2Default = {fontFamily: "Montserrat, sans-serif", fontSize: "250%"}
const menuButton3Default = {marginRight: "60%", fontFamily: "Montserrat, sans-serif", fontSize: "250%"}

export class Body extends Component {
  constructor(props){
  	super(props)
  	this.handleHomeClick = this.handleHomeClick.bind(this)
  	this.handleExpClick = this.handleExpClick.bind(this)
  	this.handlePortfolioClick = this.handlePortfolioClick.bind(this)
	this.state = {
		profile: true,
		resume: false,
		works: false,
		information: false
	}
  }

  handleHomeClick(){
  	this.setState({profile: true, resume: false, works: false, information: this.state.profile})
  }

  handleExpClick(){
  	this.setState({profile: false, resume: true, works: false, information: this.state.profile})
  }

  handlePortfolioClick(){
  	this.setState({profile: false, resume: false, works: true, information: this.state.profile})
  }
//  stickOverlay = () => this.setState({ overlayFixed: true })
  render() {
  	const menuWrapperClass = this.state.profile?"content-menu-default":"content-menu-experience"
  	const menuItemProfileClass = this.state.profile?"content-menu-item-selected":"content-menu-item"
  	const menuItemResumeClass = this.state.resume?"content-menu-item-selected":"content-menu-item"
  	const menuItemPortfolioClass = this.state.works?"content-menu-item-selected":"content-menu-item"
  	const footerWrapperClass = this.state.profile?"content-footer-default":"content-footer-experience"

  	const betweenInformation = this.state.information?"Appear":"Transition"

    return (
      <div className="web-body">
		<CSSTransitionGroup transitionName="profile" transitionEnterTimeout={500} transitionLeaveTimeout={300}
		transitionAppear={true} transitionAppearTimeout={500}>
			{this.state.profile && <Profile />}
		</CSSTransitionGroup>

		{/*Buttons*/}
		<Segment basic className="content-menu-experience">
			<Menu text widths={3} >
				<Menu.Item fitted active={this.state.profile} onClick={this.handleHomeClick}><text className="menu-button-1">About Me</text></Menu.Item>
				<Menu.Item fitted active={this.state.resume} onClick={this.handleExpClick}><text className="menu-button-2">Relevant Experience</text></Menu.Item>
				<Menu.Item fitted active={this.state.works} onClick={this.handlePortfolioClick}><text className="menu-button-3">Portfolio</text></Menu.Item>
			</Menu>		
		</Segment>

		{this.state.resume && <Resume transition={'resume' + betweenInformation}/>}
		{this.state.works && <Works transition={'works' + betweenInformation}/>}

		{/*Footer(static component) */}
		<Media query="(max-width: 700px)">
			{matches =>
			    matches ? (
					<Segment basic className="content-footer" style={{backgroundColor:"#FF7701"}}>
						Contact Me: <Icon name="mail" />
						<a href="mailto:E0035221@u.nus.edu">E0035221@u.nus.edu</a>!
					</Segment>
				):(
					<Segment basic className="content-footer" style={{backgroundColor:"#FF7701"}}>
						If you want to have chat or get more information, Contact me at <Icon name="mail" />
						<a href="mailto:E0035221@u.nus.edu">E0035221@u.nus.edu</a>!
					</Segment>
				)
			}
		</Media>
      </div>
    )
  }
}