import React from 'react'
import _ from 'lodash'
import {Menu, Sidebar, Button, Container, Segment, Image, Icon, Divider} from 'semantic-ui-react';
import {CSSTransitionGroup} from 'react-transition-group'
import Media from 'react-media'


const aboutMeExpanded = {marginTop:"2.5%", fontSize:"120%"}

export class Profile extends React.Component{
	constructor(props){
		super(props)
	}

	render(){
		return(
			<div className="background">
			<Segment basic style={{height:"8%",width:"100%",position:'auto'}} className="profile-background">
				<Segment basic className="profile-greeting-wrapper-default"> {/*shift this wrapper for animation*/}
					<Media query="(max-width: 768px)">
						{matches =>
							matches ? (
								<Image
								    floated='left'
								    circular={true}
								    src='https://preview.ibb.co/djxg8L/me.jpg'
								    className="profile-content-image"
								    size="small"
								/>
								) : (
								<Image
								    floated='left'
								    circular={true}
								    src='https://preview.ibb.co/djxg8L/me.jpg'
								    className="profile-content-image"
								    size="medium"
								/>					
								)
						}
					</Media>
					<Segment basic floated="right" className="profile-greeting-segment-default" style={{paddingLeft: "0px"}}>
						<p className="profile-greeting">Hey!<br />I'm <b>Wei Zheng</b></p>
					</Segment>
				</Segment>					            			
				<Media query="(max-width: 599px)">
					{matches =>
			            matches ? (
			            	<Segment basic className="profile-buttons-segment"> {/*shift this wrapper for animation*/}
								<Button size="medium" inverted color="orange" className="profile-button-resume" href="./documents/FWZ.pdf" download="Fong Wei Zheng">
									Download Resume
								</Button>
								<Button size="medium" inverted color="orange" className="profile-button-github" animated href="https://github.com/fongwz" target="_blank">
									<Button.Content visible>Github</Button.Content>
									<Button.Content hidden><Icon name='github' /></Button.Content>
								</Button>
							</Segment>
						):(
							<Segment basic className="profile-buttons-segment"> {/*shift this wrapper for animation*/}
								<Button size="big" inverted color="orange" className="profile-button-resume" href="./documents/FWZ.pdf" download="Fong Wei Zheng">
									Download Resume
								</Button>
								<Button size="big" inverted color="orange" className="profile-button-github" animated href="https://github.com/fongwz" target="_blank">
									<Button.Content visible>Github</Button.Content>
									<Button.Content hidden><Icon name='github' /></Button.Content>	
								</Button>
							</Segment>				
						)
					}
				</Media>
				<Container text textAlign='center' className="profile-content-about">
					<text>I love just about anything IT, from design to implementation, IoT products to Web Apps, </text>
					<text>Assembly Language to Java --  its all so intriguing.</text><br /><br />
					<text>I like designing products and then developing and implementing them, and hence, strongly believe that good design skills complement programming expertise,
					and together they can better express one's creativity.</text>
				</Container>
			</Segment>
			</div>
			);
	}
}