import React from 'react'
import {Grid,Rail,Segment,Header,Container,Icon,Divider,Image, Card, Modal} from 'semantic-ui-react'
import {CSSTransitionGroup} from 'react-transition-group'
import './Body.css'

export class Fizz extends React.Component{
	constructor(){super()}

	render(){
		return(
			<Modal closeIcon trigger={
				<Card link centered={this.props.phone} style={{width:this.props.phone?"80%":"29%"}}>
					<Image fluid size="large" src='https://cdn.pbrd.co/images/HKspJYG.jpg' />
					<Card.Content>
						<Card.Header textAlign="left">Fizz</Card.Header>
						<Card.Description>Interactive Dashboard for an Internship</Card.Description>
					</Card.Content>
				</Card>
			}>
				<Modal.Content>
					<Modal.Description>
						<Container text textAlign="center">
							<Header textAlign="center" className="works-content-modal-bigheader"><b>Overall Website View</b></Header>
							<Image fluid centered style={{width: "100%", marginTop:"3%", marginBottom:"3%"}} src='https://cdn.pbrd.co/images/HKsq8Fo.jpg' /><br />
						</Container>
						<Divider clearing><b>Frontend Design</b></Divider>
						<Container text textAlign="center" style={{marginBottom:"5%", marginTop:"1%"}}>
							<Header textAlign="center" className="works-content-modal-bigheader"><b>Target User</b></Header>	
							<p className="works-content-modal-smalltext">
								The main target audience for the dashboard were users in the business department of the company.
								Therefore, the main priority of the dashboard had to be <b>fast</b> and <b>easy</b> access
								to data.
							</p>
						</Container>
						<Container text textAlign="center" style={{marginBottom:"5%", marginTop:"5%"}}>
							<Header textAlign="center" className="works-content-modal-bigheader"><b>Summary Tab</b></Header>	
							<Image fluid centered style={{width: "100%", marginTop:"3%", marginBottom:"3%"}} src='https://cdn.pbrd.co/images/HKsqh2R.jpg' /><br />	
							<p className="works-content-modal-smalltext">
								The top right of the website had a large risk summary button to allow them a <b>quick and dirty</b>
								&nbsp;glance at key information pertaining to the website's usage.
							</p>
						</Container>						
						<Container text textAlign="center" style={{marginBottom:"5%", marginTop:"5%"}}>
							<Header textAlign="center" className="works-content-modal-bigheader"><b>Searchbar</b></Header>	
							<Image fluid centered style={{width: "100%", marginTop:"3%", marginBottom:"3%"}} src='https://cdn.pbrd.co/images/HKsqotW.jpg' /><br />	
							<p className="works-content-modal-smalltext">
								As the component with the highest usage rate, the search bar had to be <b>intuitive</b> to use. Since
								the target audience were business developers, they had a primary interesting in finding out
								numerical ranges. The search bar allowed them to either <b>search</b> for specific data, or <b>filter</b>
								&nbsp;out a broad range of data. The unconventional implementation of numerical operators allowed these developers to easily perform such actions.
							</p>
						</Container>
						<Container text textAlign="center" style={{marginBottom:"5%", marginTop:"5%"}}>
							<Header textAlign="center" className="works-content-modal-bigheader"><b>Main Table View</b></Header>	
							<Image fluid centered style={{width: "100%", marginTop:"3%", marginBottom:"3%"}} src='https://cdn.pbrd.co/images/HKsqvln.jpg' /><br />	
							<p className="works-content-modal-smalltext">
								Taking up the largest space in the website, the table had to display all relevant information to the user.
								It has all the standard implementations of a table, with a small change. Clicking on either the Customer ID
								column or the Recommended Action column brings up a modal view with a faded background.
							</p>
						<Image floated="left" style={{width: "45%", marginTop:"2.5%",marginBottom:"2%"}} src='https://cdn.pbrd.co/images/HKsqCX0.jpg' />
						<Image floated="right" style={{width: "45%", marginTop:"2.5%",marginBottom:"2%"}} src='https://cdn.pbrd.co/images/HKsqICU.jpg' />
						<Divider clearing hidden></Divider>
						<p className="works-content-modal-smalltext">
								This allows the users to zoom in on specific customers and/or update their information in the database
								more easily. Having this functionality in a on-click modal popup also saves space in a one-pager, allowing
								us to enlarge the size of the site's main feature, the data in the table.
							</p>
						</Container>																	
					</Modal.Description>
				</Modal.Content>
			</Modal>
			);
	}
}