import React from 'react'
import {Grid,Rail,Segment,Header,Container,Icon,Divider,Image, Card, Modal} from 'semantic-ui-react'
import {CSSTransitionGroup} from 'react-transition-group'
import './Body.css'

export class Crowdfunding extends React.Component{
	constructor(){ super() }

	render(){
		return(
			<Modal closeIcon trigger={
				<Card link centered={this.props.phone} style={{width:this.props.phone?"80%":"29%"}}>
					<Image fluid size="large" src='https://cdn.pbrd.co/images/HKsrwfk.jpg' />
					<Card.Content>
						<Card.Header textAlign="left">Crowdfunding Site</Card.Header>
						<Card.Description>Interactive Dashboard Project</Card.Description>
					</Card.Content>
				</Card>
			}>
				<Modal.Content>
					<Modal.Description>
						<Container text textAlign="center">
						<Header textAlign="center" className="works-content-modal-bigheader"><b>Designing a crowdfunding dashboard linked with a functional database</b></Header>
						</Container>
						<Divider hidden></Divider>
						<Container text textAlign="center" style={{marginBottom:"5%"}}>
							<p className="works-content-modal-smalltext">
								The project had 2 main components to it, the implementation of a frontend dashboard, and a backend server with a database.
								Since the module was mainly focused on a database design, less attention was paid to the frontend. Nevertheless, we
								made efforts to create a frontend design that would be pleasing to the user.
							</p>
						</Container>
						<Divider clearing><b>Frontend Design</b></Divider>
						<Container text textAlign="center" style={{marginBottom:"5%", marginTop:"5%"}}>
							<p className="works-content-modal-smalltext">
								Since majority of the efforts was put into database implementation at the start, we came up with a makeshift website prototype as shown below.
								The website was built purely using HTML and CSS.
							</p>
						</Container>
						<Image fluid centered style={{width: "70%", marginTop:"3%", marginBottom:"3%"}} src='https://cdn.pbrd.co/images/HKsrCF8.png' /><br />						
						<Image fluid centered style={{width: "70%", marginBottom:"5%"}} src='https://cdn.pbrd.co/images/HKsrIR5.png' /><br />						
						<Container text textAlign="center" style={{marginBottom:"5%", marginTop:"0%"}}>
							<p className="works-content-modal-smalltext">
								After the database implementation was complete, we revisited the original design of the site to see
								what could be improved. Referencing other modern crowdfunding sites such as Kickstarter, we decided
								that a major overhaul of the site design was needed.
							</p>
						</Container>
						<Container text textAlign="center" style={{marginBottom:"2%"}}>
							<Header textAlign="center" className="works-content-modal-bigheader"><b>Colour Scheme</b></Header>
							<p className="works-content-modal-smalltext">
								The old colour scheme was dull and boring: the login page used brown and black, two dark colours.
								This would run opposite to what the purpose of the site was: to <b>ATTRACT</b> people to invest in
								crowdfunding projects.
							</p>
							<Image fluid centered style={{width: "70%", marginTop:"3%", marginBottom:"3%"}} src='https://preview.ibb.co/fDYwq0/crowdfunding6.png'/><br />
						</Container>
						<Container text textAlign="center" style={{marginBottom:"5%"}}>
							<p className="works-content-modal-smalltext">
								We decided on a new colour scheme, by contrasting bright and dark colours, mainly black, red and white.
								The sharper contrast from this colour scheme would give the site a more "showy" and classy look, which
								would be more appealing to the eyes, therefore running in parallel to our motive, to attract would-be investors.
							</p>
							<Image fluid centered style={{width: "70%", marginTop:"3%", marginBottom:"3%"}} src='https://preview.ibb.co/bYKaiL/crowdfunding7.png'/><br />
						</Container>
						<Container text textAlign="center" style={{marginBottom:"2%"}}>
							<Header textAlign="center" className="works-content-modal-bigheader"><b>Web Elements</b></Header>
							<p className="works-content-modal-smalltext">
								Various web elements had to be redesigned as well. For example, the button to start a new project was originally
								relegated to a corner in the navigation bar at the top. This made it hard to see, and any potential
								backers might even miss the button and treat the site as defective!
							</p>
							<Image fluid centered style={{width: "70%", marginTop:"3%", marginBottom:"3%"}} src='https://image.ibb.co/g6VLYL/cf8.png'/><br />
						</Container>
						<Container text textAlign="center" style={{marginBottom:"2%"}}>
							<p className="works-content-modal-smalltext">
								Every key element in the site was redesigned to give more clarity in their functions, and every button was dramatically
								enlarged to provide easier navigation on the site. The buttons were fitted with large red icons in order to invoke a sense of imagery to the user.
								These icons serve to improve visual interest in the homepage and help guide them better, since visual aid is often more useful than
								text-based aid.
							</p>
							<Image fluid centered style={{width: "70%", marginTop:"3%", marginBottom:"3%"}} src='https://image.ibb.co/ia68nf/cf9.jpg'/><br />
						</Container>						
						<Divider horizontal clearing>Final Design</Divider>
						<Image fluid centered style={{width: "70%", marginTop:"3%", marginBottom:"3%"}} src='https://cdn.pbrd.co/images/HKsrPzh.jpg' /><br />						
						<Image fluid centered style={{width: "70%", marginBottom:"5%"}} src='https://cdn.pbrd.co/images/HKsrYfc.jpg' /><br />					
					</Modal.Description>
				</Modal.Content>
			</Modal>	
			);
	}
}