import React from 'react'
import {Grid,Rail,Segment,Header,Container,Icon,Divider,Image, Card, Modal} from 'semantic-ui-react'
import {CSSTransitionGroup} from 'react-transition-group'
import './Body.css'
import {Roominus} from './Roominus.js'
import {Crowdfunding} from './Crowdfunding.js'
import {Fizz} from './Fizz.js'
import {StickyKeys} from './Stickkeys.js'
import Media from 'react-media'

export class Works extends React.Component{
	constructor(props){
		super(props)
	}

	render(){
		return(
			<CSSTransitionGroup transitionName={this.props.transition} transitionEnter={false} transitionLeaveTimeout={500} transitionAppear={true} transitionAppearTimeout={500}>
				<div className="works-body">
			        	<Media query="(min-width: 768px)">
			        	{matches =>
			            	matches ? (
		            		<Container className="works-content">
						        <Card.Group centered itemsPerRow={3} style={{marginBottom: "3em", marginTop: "2em"}}>
						        	<Roominus phone={false}/>
									<Crowdfunding phone={false}/>
									<Fizz phone={false}/>
								</Card.Group>
								{/*<StickyKeys phone={false}/>*/}
							</Container>
						):(
							<Container className="works-content">
								<Roominus phone={true}/>
								<Crowdfunding phone={true}/>
								<Fizz phone={true}/>
								{/*<StickyKeys phone={true}/>*/}
							</Container>
						)
						}</Media>  		      
				</div>
			</CSSTransitionGroup>
			);
	}

}